import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'

import Header from "../components/Header"
import Head from "../components/Head"
import Footer from "../components/Footer"
import '../styles/App.css'

const Home = () => {
  const data = useStaticQuery(graphql`
    query {
      allContentfulBlogPost (
        sort: {
          fields: publishedDate,
          order: DESC
        }
      ) {
        edges {
          node {
            title
            slug
            publishedDate(formatString: "MMMM DD, YYYY")
          }
        }
      }
    }
  `)

  return (
    <>
      <Head title="Home" />
      <Header className="bg--secondary">
        <h1>Hello, there!</h1>
        <p>Welcome to my personal blog...</p>
        <br/>
        <p>
          This site was created using React.js and Gatsby! 
          Check out the posts section right below!
        </p>
      </Header>
      <section className="grid__container">
        <h1>Blog Posts:</h1>
        <br/>
        <ol className="blogpost__list">
          {data.allContentfulBlogPost.edges.map(edge => 
            <BlogFrame node={edge.node} />
          )}
        </ol>
      </section>
      <Footer />
    </>
  )
}

const BlogFrame = ({ node }) => {
  return (
    <li>
      <div className="blogpost__panel">
        <h2><Link to={`/blog/${ node.slug }`}>{ node.title }</Link></h2>
        <p>Posted { node.publishedDate }</p>
      </div>
    </li>
  )
}

export default Home